import React, { useEffect } from 'react';
import { BrowserRouter, Routes, Route, useNavigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Container, Typography, Box, Button, Grid, Paper, AppBar, Toolbar, IconButton } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

// Initialize Telegram WebApp
const tg = window.Telegram?.WebApp;

const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

// Home component
const Home = () => {
  const navigate = useNavigate();
  
  return (
    <Container maxWidth="sm">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          NFT Ticketing Hub
        </Typography>
        
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper sx={{ p: 3, textAlign: 'center' }}>
              <Typography variant="h6" gutterBottom>
                Welcome to NFT Ticketing Hub
              </Typography>
              <Typography variant="body1" paragraph>
                This is a Telegram Mini App for managing your NFT portfolio and event tickets.
              </Typography>
              <Button 
                variant="contained" 
                color="primary" 
                sx={{ mt: 2 }}
                onClick={() => navigate('/portfolio')}
              >
                View Portfolio
              </Button>
            </Paper>
          </Grid>
          
          <Grid item xs={12}>
            <Paper sx={{ p: 3, textAlign: 'center' }}>
              <Typography variant="h6" gutterBottom>
                Upcoming Events
              </Typography>
              <Typography variant="body1" paragraph>
                Browse and purchase tickets for upcoming events.
              </Typography>
              <Button 
                variant="contained" 
                color="secondary" 
                sx={{ mt: 2 }}
                onClick={() => navigate('/events')}
              >
                Browse Events
              </Button>
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

// Portfolio component
const Portfolio = () => {
  const navigate = useNavigate();
  
  return (
    <Container maxWidth="sm">
      <AppBar position="static" color="transparent" elevation={0}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={() => navigate('/')}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            Portfolio
          </Typography>
        </Toolbar>
      </AppBar>
      <Box sx={{ my: 4 }}>
        <Typography variant="h5" gutterBottom>
          Your NFT Portfolio
        </Typography>
        <Paper sx={{ p: 3 }}>
          <Typography variant="body1">
            Your NFTs will appear here once you connect your wallet.
          </Typography>
          <Button 
            variant="contained" 
            color="primary" 
            sx={{ mt: 2 }}
            onClick={() => tg?.showAlert('Wallet connection coming soon!')}
          >
            Connect Wallet
          </Button>
        </Paper>
      </Box>
    </Container>
  );
};

// Events component
const Events = () => {
  const navigate = useNavigate();
  
  return (
    <Container maxWidth="sm">
      <AppBar position="static" color="transparent" elevation={0}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={() => navigate('/')}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            Events
          </Typography>
        </Toolbar>
      </AppBar>
      <Box sx={{ my: 4 }}>
        <Typography variant="h5" gutterBottom>
          Upcoming Events
        </Typography>
        <Grid container spacing={2}>
          {[1, 2, 3].map((event) => (
            <Grid item xs={12} key={event}>
              <Paper sx={{ p: 2 }}>
                <Typography variant="h6">Event {event}</Typography>
                <Typography variant="body2" color="text.secondary">
                  Date: Coming Soon
                </Typography>
                <Button 
                  variant="contained" 
                  color="primary" 
                  size="small" 
                  sx={{ mt: 1 }}
                  onClick={() => tg?.showAlert('Ticket purchase coming soon!')}
                >
                  Buy Tickets
                </Button>
              </Paper>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Container>
  );
};

function App() {
  useEffect(() => {
    if (tg) {
      // Initialize Telegram WebApp
      tg.ready();
      
      // Set the header color
      tg.setHeaderColor('#1976d2');
      
      // Set the background color
      tg.setBackgroundColor('#ffffff');
      
      // Expand the WebApp to full height
      tg.expand();

      // Enable closing confirmation
      tg.enableClosingConfirmation();
    }
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/portfolio" element={<Portfolio />} />
          <Route path="/events" element={<Events />} />
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
